
import React from 'react'
import Paper from '@material-ui/core/Paper'
import { Checkbox } from '@material-ui/core'

const searchCardItem = (props) => {
    return (
        <Paper className='searchResultCard'>
            <div>
                <div className='cardPhoto'>
                    <img src='https://randomuser.me/api/portraits/men/47.jpg' alt='Pic'/>
                </div>
                <p>Featured</p>
                <p>Verified</p>

            </div>
            <div>
                <div className='cardNameWrapper'>
                    <h1>Doctor/Hospital/Clinic Name</h1>
                    <p>$500 (X% off)</p>
                </div>
                <div className='cardContactWrapper'>
                    <p>Location City</p>
                    <p>+91 999999999</p>
                    <p>username@domain.com</p>
                </div>
            </div>
        </Paper>
    )
}

export default searchCardItem