
import React, { Component } from 'react';

import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

export default class SearchFilter extends Component {
  render() {
    return (
      <div>
        <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}>
          <h5>Experties</h5>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div >
          <Select
          value={10}
          inputProps={{
            name: 'age',
            id: 'age-simple',
          }}>
            <MenuItem value={30}>All</MenuItem>
            <MenuItem value={10}>Doctor</MenuItem>
            <MenuItem value={20}>Hospital</MenuItem>
            <MenuItem value={30}>Clinic</MenuItem>
        </Select>
          </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>


        <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}>
          <h5>Select Gender</h5>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div >
            <label htmlFor='maleCheckBox'>Male  </label>
            <input type='radio' name='gender' value='Male' id='maleCheckBox' checked />
            <br/>
            <br/>
            <label htmlFor='femaleCheckBox'>Female  </label>
            <input type='radio' name='gender' value='Female'  id='femaleCheckBox'/>
          </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>


      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}>
          <h5>Select Language</h5>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div >
            <label htmlFor='hindi'>Hindi  </label>
            <input type='checkbox' name='gender' value='Male' id='maleCheckBox' />
            <br/>
            <br/>
            <label htmlFor='femaleCheckBox'>English  </label>
            <input type='checkbox' name='gender' value='Female'  id='femaleCheckBox'/>
          </div>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      </div>
    );
  }
}


