import React from "react";

import { BrowserRouter, Route } from "react-router-dom";

import Header from "./Header";
import Features from "./Features";
import Footer from "./Footer";
import VideoBg from "./VideoBg";

import Login from "./Login";
import Signup from "./Signup";
import SearchResultPage from '../components/pages/SearchResultPage'

import "../css/styles.css";
import "../css/mobile.css";
import Navigation from "./Navigation";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isChecked: "doctors",
      searchText: {
        doctors: "FIND A SUITABLE DOCTOR IN YOUR LOCATION",
        hospitals: "FIND A SUITABLE HOSPITAL IN YOUR LOCATION",
        clinics: "FIND A SUITABLE CLINIC IN YOUR LOCATION"
      },
      compareText: {
        doctors:
          "Compare: Doctor Experience, Speciality, Treatment Cost & Availability",
        clinics:
          "Compare: Doctor Experience, Specility, Treatment Cost, Availability & Clinic Setup",
        hospitals:
          "Compare: Doctor Expeience, Treatment Cost, Advanced Technology & Hospital Setup"
      },
      featureText: {
        doctors: "WE HAVE MORE THAN 10,000+ CERTIFIED DOCTORS FOR YOU",
        clinics: "WE HAVE MORE THAN 10,000+ REGISTERED CLINICS FOR YOU",
        hospitals: "WE HAVE MORE THAN 10,000+ REGISTERED HOSPITALS FOR YOU"
      }
    };
    this.onCheckboxChange = this.onCheckboxChange.bind(this);
  }
  onCheckboxChange(e) {
    if (e.target.value === "doctors") {
      this.setState(() => ({
        isChecked: "doctors"
      }));
    } else if (e.target.value === "clinics") {
      this.setState(() => ({
        isChecked: "clinics"
      }));
    } else if (e.target.value === "hospitals") {
      this.setState(() => ({
        isChecked: "hospitals"
      }));
    }
  }

  HomeComponent = () => {
    return (
      <React.Fragment>
        <Header state={this.state} onCheckboxChange={this.onCheckboxChange} />
        <Features
              feature={this.state.featureText[this.state.isChecked]}
              columnName={this.state.isChecked}
            />
      </React.Fragment>
    );
  };

  SearchResultPage = () => {
    return (
      <React.Fragment>
        {/* <div className='header-light'>
          <Navigation/>
        </div> */}
        <Header state={this.state} onCheckboxChange={this.onCheckboxChange} />
        <SearchResultPage/>
      </React.Fragment>
    )
  }

  LoginComponent = () => {
    return <Login />;
  };

  SignupComponent = () => {
    return <Signup />;
  };

  render() {
    return (
      <BrowserRouter>
          <div>
            <Route path="/" exact component={this.HomeComponent} />
            <Route path="/search" component={this.SearchResultPage} />
            <Footer />
          </div>
          {/* <Route render={<h1>Page Not Found</h1>} /> */}
      </BrowserRouter>
    );
  }
}

export default App;
