import React, { Component } from "react";

export default class VideoBg extends Component {
  constructor(props) {
    super()
    this.videoPlayer = React.createRef()
  }

  componentDidMount() {
    this.videoPlayer.current.play()
    this.videoPlayer.current.muted = true
  }

  render() {
    return (
      <div>
        <video id="bgvid" autoplay loop ref={this.videoPlayer}>
          <source src={this.props.url} type="video/webm" />
        </video>

        {this.props.children}
      </div>
    );
  }
}
