import React from 'react';

const Features = (props) => {
	const columnName = props.columnName.charAt(0).toUpperCase().concat(props.columnName.slice(1, -1)).concat('s');
	return (
		<div className="features">
			<div className="features--title">
				<h3>{props.feature}</h3>
			</div>
			<div className="features--main">
				<div className="features--column">
					<p className="features--column--title">{columnName} in Mumbai</p>
					<ul className="features--list">
						<li>FUE - Follicular Unit Extraction</li>
						<li>Robotic Hair Transplant</li>
						<li>FUE - Follicular Unit Transplant</li>
						<li>Micrografts / Minigrafts - Hair</li>
						<li>Restoration</li>
						<li>Eyebrow Transplant</li>
						<li>Eyelash Transplant</li>
						<li>Facial Hair Transplant</li>
					</ul>
				</div>
				<div className="features--column">
					<p className="features--column--title">{columnName} in Pune</p>
					<ul className="features--list">
						<li>Cancer Treatment</li>
						<li>Stem Cell Theraphy</li>
						<li>Mamogram</li>
						<li>Ovarian Cancer Treatment</li>
					</ul>
				</div>
				<div className="features--column">
					<p className="features--column--title">{columnName} in Thane</p>
					<ul className="features--list">
						<li>Knee Replacement</li>
						<li>Hip Replacement</li>
						<li>Kinesiology</li>
						<li>Posture Management</li>
						<li>Cytotron Theraphy</li>
						<li>Orthopedic Spine Surgery</li>
						<li>Ankle Injury Treatment</li>
					</ul>
				</div>
				<div className="features--column">
					<p className="features--column--title">{columnName} in Pune</p>
					<ul className="features--list">
						<li>Placement All-on-4 Dental Implants</li>
						<li>Implant Dentist Consultation</li>
						<li>Restoration of Implants</li>
						<li>Metal Free Implants</li>
						<li>Keyhole Dental Implants</li>
					</ul>
				</div>
			</div>
		</div>
	);
}


export default Features;