import React from "react";
import { Link, withRouter } from "react-router-dom";

import Compare from "./Compare";

import { Button, Checkbox } from "@material-ui/core";

const SearchForm = props => {
  const search = props.state.isChecked
    .charAt(0)
    .toUpperCase()
    .concat(props.state.isChecked.slice(1, -1));
  return (
    <div>
      <div className="search-text">
        <h2>{props.state.searchText[props.state.isChecked].toUpperCase()}</h2>
      </div>

      <div className="search-form">
        <input type="text" placeholder="Treatment Name / Speciality" />
        <input type="text" placeholder="Location" />
        <Button
            variant="contained"
            style={{
              backgroundColor: "#26ADF0",
              color: "#fff",
              fontSize: "1.8rem",
              borderRadius: "1rem",
              textDecoration: 'none',
              textTransform: "capitalize"
            }}
            type="submit"
            onClick={() => {
                props.history.push('/search')
            }}
            className="btn-search"
          >
            {search} Search
          </Button>
      </div>

      <Compare compareText={props.state.compareText[props.state.isChecked]} />

      <div className="checkboxes">
        <div className="checkbox">
          <Checkbox
            value="doctors"
            color="primary"
            id="doctors-name"
            onChange={props.onCheckboxChange}
            checked={props.state.isChecked === "doctors"}
          />
          <label htmlFor="doctors-name">Doctors</label>
        </div>

        <div className="checkbox">
          <Checkbox
            value="clinics"
            color="primary"
            id="clinics-name"
            onChange={props.onCheckboxChange}
            checked={props.state.isChecked === "clinics"}
          />
          <label htmlFor="clinics-name">Clinics</label>
        </div>

        <div className="checkbox">
          <Checkbox
            value="hospitals"
            color="primary"
            id="clinics-name"
            id="hospitals-name"
            onChange={props.onCheckboxChange}
            checked={props.state.isChecked === "hospitals"}
          />
          <label htmlFor="hospitals-name">Hospitals</label>
        </div>
      </div>
    </div>
  );
};

export default withRouter(SearchForm);
